'use server';

/**
 * @fileOverview This file defines a Genkit flow for generating step-by-step learning guides for web3 skills or roles.
 *
 * - generateLearningGuide - A function that takes a skill or role as input and returns a learning guide.
 * - GenerateLearningGuideInput - The input type for the generateLearningGuide function.
 * - GenerateLearningGuideOutput - The return type for the generateLearningGuide function.
 */

import {ai} from '@/ai/genkit';
import {z} from 'genkit';

const GenerateLearningGuideInputSchema = z.object({
  skillOrRole: z
    .string()
    .describe('The specific skill or role for which to generate a learning guide.'),
});
export type GenerateLearningGuideInput = z.infer<typeof GenerateLearningGuideInputSchema>;

const GenerateLearningGuideOutputSchema = z.object({
  learningGuide: z.string().describe('A step-by-step guide for learning the specified skill or role.'),
});
export type GenerateLearningGuideOutput = z.infer<typeof GenerateLearningGuideOutputSchema>;

export async function generateLearningGuide(
  input: GenerateLearningGuideInput
): Promise<GenerateLearningGuideOutput> {
  return generateLearningGuideFlow(input);
}

const generateLearningGuidePrompt = ai.definePrompt({
  name: 'generateLearningGuidePrompt',
  input: {schema: GenerateLearningGuideInputSchema},
  output: {schema: GenerateLearningGuideOutputSchema},
  prompt: `You are an expert in web3 technologies and career paths. Generate a comprehensive, step-by-step learning guide for the following skill or role: {{{skillOrRole}}}. The guide should include specific resources, tutorials, and documentation where possible. Focus on providing the most up-to-date and effective approaches to learning.`,
});

const generateLearningGuideFlow = ai.defineFlow(
  {
    name: 'generateLearningGuideFlow',
    inputSchema: GenerateLearningGuideInputSchema,
    outputSchema: GenerateLearningGuideOutputSchema,
  },
  async input => {
    const {output} = await generateLearningGuidePrompt(input);
    return output!;
  }
);
